import { Toast } from 'fx-mui';

/**
 * undefined,null,NaN,'',[],{} empty
 *
 * @param {*} val
 */
export const isEmpty = val => {
    let type = Object.prototype.toString.call(val)
    if (typeof val === 'undefined' || val === null || Number.isNaN(val)) {
        return true
    }
    if (type === '[object Array]' || type === '[object String]') {
        return val.length === 0
    }
    if (
        type === '[object Number]' ||
        type === '[object Boolean]' ||
        type === '[object RegExp]' ||
        type === '[object Function]' ||
        type === '[object Date]' ||
        type === '[object Error]'
    ) {
        return false
    }
    return Object.keys(val).length === 0
}

var defaultValidate = (val, type) => {
    var patternStr = '';
    switch (type) {
        case 'mobilePhone':
            patternStr = /^1[345678]\d{9}$/;
            if (!(patternStr.test(val))) {
                return false;
            }
            break;
        case 'email':
            patternStr = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/;
            if (!(patternStr.test(val))) {
                return false;
            }
            break;
        case 'phone':
            patternStr = /^(\d{3}|\d{4})(-)?(\d{7}|\d{8})/;
            if (!(patternStr.test(val))) {
                return false;
            }
    }
    return true;
}

export const validation = function (arr, _toast) {
    //arr, type(mobilePhone\phone\email)
    //msg()callback()required()value();
    //_toastUItoastmsg 
    var t;
    t = Toast;
    for (let item of arr) {
        if (item.required) {
            if (!item.value) {
                t(item.nullMsg || '');
                return false;
            }
        }
        if (item.callback) {
            var c = item.callback();
            if (!c) {
                t(item.msg || "");
                return false;
            }
        }
        if (item.type == 'mobilePhone' || item.type == 'phone' || item.type == 'email') {
            var dv = defaultValidate(item.value, item.type, t);
            if (!dv) {
                if (item.msg) {
                    t(item.msg);
                } else {
                    switch (item.type) {
                        case 'mobilePhone':
                            t("");
                            break;
                        case 'phone':
                            t("");
                            break;
                        case 'email':
                            t("");
                    }
                }
                return false
            }
        }
    }
    return true;
}




