export default {
    USER_LOGIN_STATUS: 'user.login.status', //
    USER_LOGIN_INFO: 'user.login.info', //    
    setItem(name, content) {
        if (!name) return
        // content undefined
        if (typeof content === 'undefined') throw TypeError('sessionStorage.setItemundefined')
        window.sessionStorage.setItem(name, JSON.stringify(content))
    },
    getItem(name) {
        if (!name) return
        return JSON.parse(window.sessionStorage.getItem(name))
    },
    removeItem(name) {
        if (!name) return
        window.sessionStorage.removeItem(name)
    },
    getUserInfo() {
        return this.getItem(this.USER_LOGIN_INFO);
    }
}